/* ////////// PAGE DES POSTS ////////// */

/* ////////// ENTETE ////////// */

.entete {
  max-width: 1100px;
  margin-top: 68px;
  background: var(--colorier-light-gray);
  padding: 10px;
}

.entete a {
  color: var(--colorier-dark-gray);
}
.entete a:hover {
  color: var(--colorier-noir) !important;
}

.entete .acces {
  font-size: 12px;
  text-transform: uppercase;
  margin: 15px;
}

.entete .infos {
  position: relative;
  display: flex;
  align-items: center;
}

.entete .infos .productofday {
  position: absolute;
  right: 20px;
  font-size: 12px;
  display: flex;
  align-items: flex-start;
  border: 1px solid #ddd;
  border-radius: 3px;
  background: var(--colorier-light-gray);
  padding: 5px 10px;
}

.entete .infos .productofday:hover {
  cursor: pointer;
}

.entete .infos .text-infos {
  font-size: 11px;
}

.entete .infos .text-infos div:nth-child(2) {
  margin-top: 15px;
}

.entete .infos .text-infos span {
  margin: 5px;
  font-size: 11px;
  color: lightgray;
  top: -2px;
  cursor: pointer;
}

.entete .infos .text-infos p {
  margin: 10px 0;
}

.entete .infos .text-infos div a {
  font-size: 20px;
  color: var(--colorier-noir);
  font-weight: 600;
}

.entete .infos .gif-infos {
  margin: 15px;
}

.entete .infos .text-infos #webapp,
.entete .infos .text-infos #morewebapp,
.entete .infos .text-infos #health,
.entete .infos .text-infos #morehealth {
  border: 1px solid #ddd !important;
  height: 24px;
  padding: 5px 8px;
  font-size: 11px;
  font-weight: 400 !important;
  text-align: center;
  color: var(--colorier-dark-gray);
  display: inline;
  text-decoration: none;
  border-radius: 3px;
  background: var(--colorier-blanc);
}

.entete .infos .text-infos #morewebapp,
.entete .infos .text-infos #morehealth {
  display: none;
}

.entete .infos .text-infos #health,
.entete .infos .text-infos #webapp {
  margin-right: 10px;
}

.entete .infos .text-infos #webapp:hover + #morewebapp,
.entete .infos .text-infos #health:hover + #morehealth {
  display: inline !important;
  margin-right: 10px;
}

#plusdeux {
  font-size: 12px;
  color: var(--colorier-dark-gray);
  font-weight: normal;
  margin-left: 4px;
}

.entete .infos .text-infos #webapp:hover,
.entete .infos .text-infos #morewebapp:hover,
.entete .infos .text-infos #health:hover,
.entete .infos .text-infos #morehealth:hover {
  background: #ddd;
  color: var(--colorier-dark-gray) !important;
}

#morewebapp {
  display: none;
}

.entete .infos .text-infos #webapp:hover {
  display: inline;
}

/* ////////// CORPUS PAGEPOST ////////// */

.parentpost {
  display: flex;
  max-width: 1100px;
  margin: auto;
  padding: 10px;
}

.listepost {
  width: 69%;
  margin: 20px auto;
  background: transparent;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.canvas {
  background: var(--colorier-blanc);
  width: 99%;
  display: flex;
  flex-direction: column;
  padding: 15px 10px;
  border-radius: 5px;
  box-shadow: 0 0 4px #ddd;
  margin: 0 auto;
}

.listepost .canvas #thumbnail-post ul {
  list-style: none;
  margin: 10px 5px;
  display: flex;
  padding: 0;
}

.listepost .canvas #thumbnail-post ul li {
  border: 1px solid #ddd;
  width: 40px;
  margin: 0 5px;
  cursor: pointer;
}

.listepost .canvas #description-post {
  font-size: 12px;
  padding: 5px 10px;
}

.listepost .canvas #partage-post {
  margin: 5px 5px 15px 5px;
}

.listepost .canvas #partage-post #upvote {
  text-align: center;
  background: var(--colorier-dark-red);
  border: none;
  border-radius: 3px;
  padding: 10px 15px;
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  margin-left: 5%;
}
.listepost .canvas #partage-post #upvote:hover {
  background: var(--colorier-flash-red);
}

.listepost .canvas #partage-post #upvote #votes {
  font-weight: normal;
  opacity: 0.5;
  margin-left: 5px;
}

.listepost .canvas #partage-post #twitter-btn {
  padding: 5px 25px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background: #00aced;
}

.listepost .canvas #partage-post #facebook-btn {
  padding: 5px 25px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background: #3b5998;
}

.listepost .canvas #partage-post #more-btn {
  padding: 5px 25px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background: var(--colorier-noir);
}

@media only screen and (max-width: 768px) {
  .entete {
    background: var(--colorier-blanc);
    height: 364px;
  }

  .entete .infos {
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    height: 300px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    text-align: center;
  }

  .entete .infos div {
    margin: 0 auto;
  }

  entete .infos .text-infos {
    height: 95px !important;
  }

  .entete .infos .productofday {
    position: relative;
    margin: auto;
    right: auto;
  }

  .listepost {
    width: 100%;
    padding: 0;
  }

  .listepost .canvas {
    width: 100%;
  }
}

/* ////////// COMMENTAIRES ////////// */

#inred {
  color: var(--colorier-dark-red) !important;
  font-weight: 600;
  text-decoration: inherit;
  font-size: 11px;
}

#kj {
  font-size: 13px;
  text-transform: uppercase;
  color: var(--colorier-dark-gray);
  line-height: 16px;
}

.commentaires {
  background: var(--colorier-blanc);
  width: 99%;
  display: flex;
  flex-direction: column;
  padding: 15px 10px;
  border-radius: 5px;
  box-shadow: 0 0 4px #ddd;
  margin: 0 auto;
}

.commentaires .recommend {
  font-size: 12px;
  margin: 0 10px;
}

.commentaires .recommend:nth-child(1) {
  border-bottom: 1px solid var(--colorier-light-gray);
  padding-bottom: 20px;
}

.commentaires .recommend:last-child {
  border-top: 1px solid var(--colorier-light-gray);
  padding-top: 20px;
}

.commentaires .recommend #btn-yes,
.commentaires .recommend div:nth-child(2) a {
  border: 1px solid transparent;
  background: var(--colorier-light-green);
  border-radius: 3px;
  color: var(--colorier-dark-green);
  font-weight: 600;
  padding: 3px 5px;
  margin-left: 8px;
}
.commentaires .recommend #btn-yes:hover {
  border: 1px solid var(--colorier-dark-green);
}

.commentaires .recommend #btn-no {
  border: 1px solid transparent;
  background: var(--colorier-light-red);
  border-radius: 3px;
  color: var(--colorier-dark-red);
  font-weight: 600;
  padding: 3px 5px;
  margin-left: 8px;
}
.commentaires .recommend #btn-no:hover {
  border: 1px solid var(--colorier-dark-red);
}

.commentaires .recommend div:nth-child(1) {
  margin-bottom: 10px;
}

.commentaires .recommend div:nth-child(2) label {
  font-weight: normal;
  margin-right: 15px;
}

.commentaires .recommend form {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
}

.commentaires .recommend form img {
  border-radius: 50%;
  margin: 15px 15px 15px 0;
}

.commentaires .recommend form button {
  background: var(--colorier-dark-red);
  border: none;
  border-radius: 3px;
  padding: 8px 15px;
  color: #fff;
  font-weight: 600;
  position: absolute;
  right: 15px;
}
.commentaires .recommend form button:hover {
  background: var(--colorier-flash-red);
}

.commentaires .recommend form input[type="text"] {
  height: 32px;
  border: 1px solid #ddd;
  padding: 5px 10px;
  border-radius: 3px;
  width: 75%;
}

.commentaires .discussions {
  padding: 20px;
}

.commentaires .discussions #discussion {
  display: block;
  margin: 10px 0;
}

.commentaires .discussions #discussion div:nth-child(1) img {
  border-radius: 50%;
}

.commentaires .discussions #discussion div:nth-child(1) a {
  margin-left: 5px;
  color: var(--colorier-noir);
  font-size: 13px;
  font-weight: 600;
}

.commentaires .discussions #discussion div:nth-child(2) {
  margin-left: 40px;
  font-weight: normal;
  font-size: 11px;
}

.commentaires .discussions #discussion a {
  margin: 0 20px;
  text-decoration: none;
}

.commentaires .discussions #discussion div:nth-child(2) span:nth-child(4) {
  font-weight: 300;
}

:root {
  --colorier-blanc: #fff;
  --colorier-noir: #000;
  --colorier-dark-red: #cc4d29;
  --colorier-light-red: #feeded;
  --colorier-flash-red: #ff4d29;
  --colorier-light-gray: #f3f3f3;
  --colorier-dark-gray: #777777;
  --colorier-light-green: #f1f8e5;
  --colorier-dark-green: #429861;
}

body {
  background: #f3f3f3 !important;
  background: var(--colorier-light-gray) !important;
  font-family: "Poppins", "roboto", sans-serif !important;
  font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;
}

/* ////////// HEADER ///////// */
nav {
  background: #fff !important;
  background: var(--colorier-blanc) !important;
  min-height: 67px;
  overflow: hidden;
  line-height: 20px;
  white-space: nowrap;
}

nav .container {
  max-width: 1100px;
  min-height: inherit !important;
}

.logo svg {
  margin: 14px;
}

#header-gauche {
  height: 68px;
}

#header-navig {
  min-height: inherit;
}

#header-navig ul {
  min-height: 67px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#touche-navig li:hover {
  font-weight: 600;
}

#header-search {
  position: relative;
  padding: 0 5px;
}

#header-search input {
  height: 25px;
  width: inherit !important;
  box-sizing: border-box;
  height: 35px;
  padding: 0 10px 0 32px;
  border: 1px solid #e8e8e8;
  border-radius: 3px;
  font-size: 11px;
  font-weight: 400;
}

#header-search .glyphicon {
  position: absolute;
  margin: 9px;
  color: #d2d2d2;
}

#connecter {
  font-size: 10px;
  font-weight: bold;
}

#login,
#signup {
  margin: 5px;
  width: 60px;
  border-radius: 4px;
  height: 30px;
  display: flex; /*pour effectuer les precedents*/
  justify-content: center; /*Horizontalement*/
  align-items: center; /*verticalement*/
  border: 1px solid #f3f3f3;
  border: 1px solid var(--colorier-light-gray);
}

#login {
  background: #fff;
  background: var(--colorier-blanc);
  margin-right: 5px;
}
#login:hover {
  background: #f1f8e5;
  background: var(--colorier-light-green);
}

#signup {
  color: #cc4d29 !important;
  color: var(--colorier-dark-red) !important;
}

#signup:hover {
  background: #feeded;
  background: var(--colorier-light-red);
}

@media only screen and (max-width: 768px) {
  #header-navig ul {
    display: inline;
  }
  #connecter {
    font-size: 10px;
    font-weight: bold;
    justify-content: left;
  }
  #login,
  #signup {
    width: 100%;
    margin: 15px;
  }
}

.parent {
  display: flex;
  margin-top: 70px !important;
  max-width: 1100px;
  margin: auto;
  padding: 10px;
}

.principal {
  width: 70%;
}

.liste {
  padding: 5px;
}

.sidebar {
  width: 30%;
  background: #000;
  color: #fff;
}

@media only screen and (max-width: 768px) {
  .sidebar {
    display: none;
  }
  .principal {
    width: 100%;
  }
}

/* ////////// POSTES ///////// */

.holder_f55f6 {
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    color-stop(0, #f2f2f2),
    color-stop(20%, #e3e3e3),
    color-stop(40%, #f2f2f2),
    to(#f2f2f2)
  );
  background-image: linear-gradient(
    90deg,
    #f2f2f2 0,
    #e3e3e3 20%,
    #f2f2f2 40%,
    #f2f2f2
  );
  background-repeat: no-repeat;
  background-size: 800px 80px;
  content: " ";
  display: block;
  height: 100%;
  position: relative;
  -webkit-animation: placeholderAnimation_1f61f 1s infinite;
  animation: placeholderAnimation_1f61f 1s infinite;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.liste h1 {
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

.liste .posts .post:nth-child(1) {
  border-radius: 5px 5px 0 0;
}

.liste .posts .post {
  position: relative;
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  background: #fff;
  background: var(--colorier-blanc);
  padding: 10px;
  height: 120px;
  cursor: pointer !important;
}

.liste .posts .post:hover {
  background: #f9f9f9;
}

.liste .posts .post #div1 {
  margin-left: 10px;
}

.liste .posts .post #div2 {
  height: inherit !important;
  width: inherit;
}

.liste .posts .post #div2 h2 {
  font-size: 14px;
  font-weight: bold;
}

.liste .posts .post #div2 p {
  font-size: 12px;
}

.liste .posts .post #div2 #comments,
.liste .posts .post #div2 #edit {
  border: 1px solid #f3f3f3;
  border: 1px solid var(--colorier-light-gray);
  height: 24px;
  padding: 0 8px;
  margin-right: 5px;
  font-size: 11px;
  font-weight: 600px;
  color: #777777;
  color: var(--colorier-dark-gray);
  display: inline;
}

.liste .posts .post #div2 #edit {
  display: none;
}
.liste .posts .post:hover #div2 #edit {
  display: inline;
}

.liste .posts .post #div2 #legal {
  color: #777777;
  color: var(--colorier-dark-gray);
  font-size: 11px;
}

.liste .posts .post #div2 #comments:hover,
.liste .posts .post #div2 #edit:hover {
  background: #f3f3f3;
  background: var(--colorier-light-gray);
  cursor: pointer !important;
}

.liste .posts .post #div3 {
  position: absolute;
  height: 74px;
  width: 64px;
  border-radius: 5px;
  border: 1px solid #ddd;
  display: flex;
  justify-content: center;
  right: 20px;
  background: #fff;
}

#div1 {
  margin-right: 15px;
}

#div3 span {
  position: absolute;
  margin-top: 20px;
}

#div3 label {
  position: absolute;
  font-weight: normal;
  font-size: 12px;
  bottom: 12px;
}

.sidebar {
  width: 30%;
  background: #000;
  color: #fff;
  padding: 10px;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .sidebar {
    display: none;
  }
}

